<template>
  <div class="project-comment-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="commentArr"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <pem-comment-list :list="commentArr" />
          <div class="scroll-view-pullup">{{isMore ? '加载中...' : '暂时就这么多了...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemCommentList from 'components/comment-list/comment-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { dateFormat, setTitle } from 'common/js/util'
import { MiniSiteApi, ImgPath } from 'api'

export default {
  components: {
    PemCommentList,
    PemScrollView,
    PemLoading
  },
  data () {
    return {
      projectzj: '',
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      commentArr: [],
      isMore: true,
      isLoading: true
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-点评')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.projectzj = this.$route.params.projectzj.toString()
      this.getCommentlist('init')
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getCommentlist('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getCommentlist('init')
      }
    },
    getCommentlist (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      } else {
        if (this.currentPage < this.totalPage) {
          this.currentPage = +this.currentPage + 1
        } else {
          this.isMore = false
          return
        }
      }
      this._getCommentlist({
        projectzj: this.projectzj,
        currentPage: +this.currentPage,
        pageSize: +this.pageSize,
        position: 'list'
      }, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = +data.currentPage || 1
          this.totalPage = +data.totalPage
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _commentArr = (type === 'init') ? [] : this.commentArr
          _commentArr = _commentArr.concat(data.resultList || []).map(item => {
            item['time'] = dateFormat(item['createtime'])
            if (item['userlogo']) {
              item['logo'] = ImgPath + item['userlogo']
            } else {
              let userZj = (item.userzj || item.empzj).toUpperCase()
              let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
              item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
            }
            return item
          })
          this.commentArr = _commentArr
        }
      })
    },
    _getCommentlist (options, callback) {
      MiniSiteApi.getCommentlist(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>



